import React from 'react'
import Social from '../../blocks/social'

const Tile3 = (props) => {
    const {type, categories, levels, selected, className} = props;
    const {comments, votes, shared} = props;
    
    const {post = {}} = props;
    const {title, text, podcast, image, videoId} = post;

    const getTypes = () => {

    }

    const getImg = () => {
        if (image) {
            return <img src={`/media/images/${image}`} width={`269px`} />
        }
    }

    const getVideo = () => {
        if (videoId) {

         return (   
            
            <iframe type="text/html" width={`${full?480:300}`} 
            src={`https://www.youtube.com/embed/${videoId}?enablejsapi=1`}
            frameBorder="0"
            id={`video${videoId}`}></iframe>

            
           
);
        }
    }


    return (
        <div className={`cell size3 ${selected ? 'selected' : ''} ${className || ''}`}>
            <div className="header">
                {getTypes()} {title}
            </div>
            <div className="content">
                <div className="body">
                    
                    <div className="img">{getImg()}</div>
                    <div>{text}</div>

                
                </div>
                <div className="footer">
                    
                <Social social={ {
        nbComments: 0,
        nbVotes: {
            up: 0,
            down: 0
        },
        nbShared: 0,
        levels: post.levels,
        }} />
                    
                </div>
            </div>



            <style jsx> 
        {`
        
        .content {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            height: 525px;
        }

        .cell:last-of-type {
            margin-right: 0px;
        }
        .row:last-of-type {
            margin-bottom: 0px;
        }
        .cell {
            background-color: #F4F6F6;
            border: 1px solid #F0F2F2;
            border-radius: 5px;
            margin-right: 11px;
            height: 282px;

            color: #707070;
            letter-spacing: 0;
        }

        .cell:hover, .selected {   
            background-color: #D3DDDD;
            border: 1px solid #4E7876;
            color: #000000;
            opacity: 1;
        }

        .img {
            margin-bottom: 55px;
            opacity: 1;
        }
        .cell:hover > .content > .body > .img {
            opacity: 1;
        }

        .cell.size1 {
            width: 325px;
        }

        .cell.size2 {
            width: 663px;
        }

        .cell.size3 {
            width: 325px;
            height: 577px;
        }

        .cell.featured {
            border: 1px solid #C2CCCC;
        }
        .featured .header {
            border-bottom: 2px solid #D3DDDD;
        }

        .cell.available {
            border: 2px dashed #C2CCCC;
            background-color: #fff; 
            width: 323px;
            height: 280px;
        }
        
        .available .header {
            border-bottom: 2px dashed #D3DDDD;
        }

        .row {
            display: flex;
            margin-bottom: 11px;
        }
        
        .row.colspan { 
            flex-flow: wrap;
            width: 676px;
            margin-bottom: 0px !important;
        }

        .row.colspan .cell { 
            margin-bottom: 11px;
        }

        .last { 
            margin-bottom: 0px !important;
        }

        .header, .body, .footer {
            padding: 0px 28px 0px 28px;
        }


        .body {
            margin-top: 13px;
            font: 12px/17px Arial;
        }

        .actions {
            height: 100%;
            align-items: center;
            display: flex;
            flex-direction: row;
        }

        .footer {
            align-items: center;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
        }

        .header {
            height: 100%;
            align-items: center;
            display: flex;
            flex-direction: row;

            height: 40px;
            border-bottom: 2px solid #FFFFFF;
            
            font: Bold 16px/19px Arial;
        }

        `}
            
        </style>
        
        </div>
    );

}

export default Tile3;